<template>
  <div id="app">
    <div class="block" v-drag>
    <div class="grabbable" id="cc">
    <p> {{ value.length }} / {{ value.trim().split(/\s+/).length }} </p>
    </div>
    <div id="fr">
    <text-reader @load="value = $event"></text-reader>
    </div>
    </div>
    <div id="editor">
    <mavon-editor style="height: 100%" @save="save" v-model="value" language="en" :toolbars="toolbars" placeholder="Á∑®ÈõÜ„ÇíÂßã„Å¶„Å≠ÔºÅ" defaultOpen="edit" :subfield=false />
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import TextReader from "./TextReader";
import drag from '@branu-jp/v-drag';
export default {
  name: 'Annin',
  data() {
    return {
      value: '',
      toolbars: {
      bold: false, // Á≤ó‰Ωì
      italic: false, // Êñú‰Ωì
      header: false, // Ê†áÈ¢ò
      underline: false, // ‰∏ãÂàíÁ∫ø
      strikethrough: false, // ‰∏≠ÂàíÁ∫ø
      mark: false, // Ê†áËÆ∞
      superscript: false, // ‰∏äËßíÊ†á
      subscript: false, // ‰∏ãËßíÊ†á
      quote: false, // ÂºïÁî®
      ol: false, // ÊúâÂ∫èÂàóË°®
      ul: false, // Êó†Â∫èÂàóË°®
      link: false, // ÈìæÊé•
      imagelink: false, // ÂõæÁâáÈìæÊé•
      code: false, // code
      table: false, // Ë°®Ê†º
      fullscreen: false, // ÂÖ®Â±èÁºñËæë
      readmodel: true, // Ê≤âÊµ∏ÂºèÈòÖËØª
      htmlcode: false, // Â±ïÁ§∫htmlÊ∫êÁ†Å
      help: false, // Â∏ÆÂä©
      /* 1.3.5 */
      undo: false, // ‰∏ä‰∏ÄÊ≠•
      redo: false, // ‰∏ã‰∏ÄÊ≠•
      trash: false, // Ê∏ÖÁ©∫
      save: true, // ‰øùÂ≠òÔºàËß¶Âèëevents‰∏≠ÁöÑsave‰∫ã‰ª∂Ôºâ
      /* 1.4.2 */
      navigation: false, // ÂØºËà™ÁõÆÂΩï
      /* 2.1.8 */
      alignleft: false, // Â∑¶ÂØπÈΩê
      aligncenter: false, // Â±Ö‰∏≠
      alignright: false, // Âè≥ÂØπÈΩê
      /* 2.2.1 */
      subfield: true, // ÂçïÂèåÊ†èÊ®°Âºè
      preview: false, // È¢ÑËßà
      }
    }
  },
  methods: {
    save: function () {
      var blob = new Blob([ this.value ], {'type': 'text/plain'});
      let link = document.createElement('a');
      link.href = window.URL.createObjectURL(blob);
      link.download = moment(new Date).format('YYYY-MM-DD ddd HH-mm-ss') + '.md';
      link.click();
      window.URL.revokeObjectURL(link);
    }
  },
  components: {
    TextReader
  },
  directives: {
    drag
  }
}
</script>
  
